  color: #ccc;
}
.footer {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
}
.footer_about {
  /*   background:red; */
}
h3 {
  margin-bottom: 1rem;
}
