  padding: 1rem 0 0 0;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

@media screen and (max-width: 700px) {
  .footer {
    grid-template-column: 1fr;
  }
}

#videoContainer {
  position: relative;
  background-color: black;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  min-height: 25rem;
}

#videoContainer video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  object-fit: cover;
  z-index: 0;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}
.overlay {
  position: absolute;
  background-color: black;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  opacity: 0.5;
  z-index: 1;
}
